* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'TT Norms', sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.content {
  background-image: linear-gradient(to bottom, rgb(25, 77, 77) 0%, rgb(48, 48, 48) 100%);
  flex: 1 1 auto;
}

.form {
  margin: 0 auto;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  &__wrapper {
    max-width: 390px;
    width: 100%;
    padding: 30px 15px;
    background-color: rgb(51, 82, 82);
    border-radius: 15px;
    box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.1);
  }

  &__title {
    font-size: 20px;
    color: #fff;
    line-height: 1.2;
    letter-spacing: 0.04em;
    text-align: center;
  }

  &__content {
    padding: 15px 37px;
  }

  &__btn {
    margin: 25px 0;
    &_enter{
      margin-bottom: 0;
    }
  }

  &__agree {
    font-size: 13px;
    text-align: center;
    color: #fff;

    &.checkbox {
      text-align: left;
    }

    a {
      text-decoration: underline;
      text-decoration-style: dashed;
    }
  }

  &-item {
    &-filled {
      .form-item__placeholder {
        bottom: 38px;
        font-size: 12px;
      }
    }

    &__label {
      height: 64px;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-end;

      padding: 5px 0 10px 0;
    }

    &__placeholder {
      position: absolute;
      left: 0;
      bottom: 18px;

      font-size: 14px;
      color: rgba(255, 255, 255, 0.5);
      transition: .3s bottom linear, .3s font-size linear;
    }

    &__input {
      width: 100%;
      line-height: 1.2;
      box-shadow: none;
      position: relative;
      color: rgb(255, 255, 255);
      border: none;
      border-bottom: 1px solid rgba(255, 255, 255, 0.26);
      border-radius: 0;
      padding: 0 0 8px 0;
      font-size: 14px;
      cursor: initial;
      background-color: transparent;
      transition: .3s border-color linear;
      outline: none;

      &:focus {
        border-color: rgb(59, 59, 59);

        & ~ .form-item__placeholder {
          bottom: 38px;
          font-size: 12px;
        }
      }
    }
  }
}

.tabs {
  &-nav {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 35px;

    &__item {
      margin: 0 15px;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        height: 3px;
        width: 0;
        display: block;
        background-color: #fff;
        bottom: -8px;
        left: 0;
        transition: .3s width;
      }

      &.active {
        &::before {
          width: 100%;
        }
      }
    }

    &__link {
      font-size: 20px;
      color: #fff;
      letter-spacing: 0;
      line-height: 1.4;
      text-transform: uppercase;
    }
  }

  &-content {
    &__item {
      display: none;

      &.active {
        display: block;
      }
    }
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;

  border: none;
  outline: none;
  height: 50px;

  font-size: 20px;
  color: #fff;
  text-transform: uppercase;

  border-radius: 5px;

  transition: .3s background-color linear;

  &:hover {
    cursor: pointer;
  }

  &-green {
    width: 100%;
    background-color: rgb(0, 157, 81);

    &:hover {
      background-color: rgb(0, 207, 255);
    }
  }

  &-yellow {
    background-color: rgb(255, 107, 0);
    max-width: 210px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;

    &:hover {
      background-color: rgb(207, 204, 0);
    }
  }
}

.checkbox {

  &__input {
    display: none;

    &:checked ~ .checkbox__checkmark {
      background-color: rgb(59, 59, 59);
    }
  }

  &__label {
    &:hover {
      cursor: pointer;
    }
  }

  &__text {
    font-size: 14px;
    color: #fff;
  }

  &__checkmark {
    width: 18px;
    height: 18px;
    margin: 0 4.5px 0 0;
    display: inline-block;
    background: #fff;
    border: 2px solid rgba(0, 0, 0, 0.25);
    position: relative;
    border-radius: 2px;

    transition: .3s background-color linear;
  }
}

.forgot-password {
  font-size: 14px;
  color: rgb(201, 201, 201);
  transition: .3s color;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  margin:10px 0;
  &:hover {
    color: #ffffff;
  }
}