@media screen and (max-width: 600px){
  .footer{
    &-list{
      display: grid;
      grid-template-columns: repeat(3,minmax(0,1fr));
      row-gap: 10px;
      &__item:nth-child(4){
        grid-column-start: 1;
        grid-column-end: 4;
      }
    }
  }
}

@media screen and (max-width: 390px) {
  .footer{
    padding-top: 20px;
  }
  .footer-list{
    row-gap: 40px;
    &__item{
      &:nth-child(1){
        display: none;
      }
      &:nth-child(2){
        justify-content: flex-start;
      }
      &:nth-child(3){
        grid-column-start: 2;
        grid-column-end: 4;
        justify-content: flex-end;
      }
    }
    &__link{
      font-size: 10px;
    }
  }
  .form{
    &__content{
      padding: 0 10px;
    }
  }
  .btn{
    height: 45px;
    font-size: 16px;
    &-yellow{
      max-width: 100%;
    }
  }
}